import { injectGlobal } from 'styled-components';

injectGlobal`

  @import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:300');

  body {

    font-family: 'Source Sans Pro', sans-serif;

    padding : 0;
    margin : 0;

    line-height : 1.5;

    margin-top : 5rem;

  }

`;


